// 公共组件
// import getUserInfoButton from "@/components/getUserInfoButton.vue";
import Index from "./index-model.js";
const index = new Index();
export default {
	data() {
		return {
			title: 'Hello',
			authorizationButton: null,
			userInfo: {},
			userInfoAll: {},
			weRunData: {
				calorie: "0", // 昨日消耗卡路里
				lastDayStep: 0, // 昨日步数
				todayStep: 0 // 今日步数
			}
		}
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	onShow() {
		// 获取已授权类别
		const that = this
		uni.getSetting({
			success(res) {
				if (res.authSetting['scope.userInfo']) {
					// 隐藏授权按钮
					that.authorizationButton = false;
					that.$store.commit('updateAuthorizationButtonData', false);
				}
			},
			fail() {
				console.log("获取授权信息授权失败")
			}
		})
		// let token = index.get_storage('token_type', callBack);
		// if(token) {
		// 	that.getUserInfo(() => {
		// 		that.getRunData(() => {
		// 			callBack && callBack();
		// 		})
		// 	})
		// }
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			that.wx_login(() => {
				that.getUserInfo(() => {
					if (that.userInfo.type == 'member') {
						// 会员进入兑换步数
						that.getRunData(() => {
							that.runExchangeBeans(() => {
								callBack && callBack();
							})
						})
					} else {
						// 提示用户非会员
						that.promptOpenVip()
					}
				})
			})
		},
		exchangeBeans(callBack) {
			const that = this
			that.runExchangeBeans(() => {
				that.getRunData(() => {
					callBack && callBack();
				})
			})
		},
		wx_login(callBack) {
			const that = this;
			that.authorizationButton = that.$store.state.authorizationButton;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					var code = loginRes.code;
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							that.userInfoAll = infoRes
							that.$store.commit('updateUserInfo', that.userInfo);
							that.$store.commit('updateAuthorizationButtonData', false);
							index.login({
								code: code,
								share_id: '',
								headimgurl: infoRes.userInfo.avatarUrl,
								nickname: infoRes.userInfo.nickName,
								sex: infoRes.userInfo.gender
							}, (res) => {
								if (res.status_code == 'ok') {
									index.set_storage('token', res.access_token);
									index.set_storage('token_type', res.token_type);
								}
								callBack && callBack();
							})
						}
					})
				}
			});
		},
		// 用户信息获取
		getUserInfo(callBack) {
			const that = this
			index.getUserInfo({}, (res) => {
				if (res.status_code == 'ok') {
					let userInfo = that.$store.state.userInfo;
					that.userInfo = Object.assign(userInfo, res.data)
					that.$store.commit('updateUserInfo', that.userInfo);
					that.$store.commit('updataSettingsInfo', res.settings);
				}
				callBack && callBack();
			})
		},
		// 获取步数
		getRunData(callBack) {
			wx.getWeRunData({
				success: res => {
					const that = this
					index.getRunData({
						iv: res.iv,
						encryptedData: res.encryptedData,
						signature: that.userInfoAll.signature,
						rawData: that.userInfoAll.rawData
					}, (res) => {
						if (res.status_code == 'ok') {
							that.weRunData = res.data
						}
						callBack && callBack();
					})
				}
			})
		},
		
		// 兑换步数
		runExchangeBeans(callBack) {
			const that = this
			index.exchangeBeans({}, (res) => {
				// 提示
				index.show_tips(res.message)
				if (res.status_code == 'ok') {
					that.weRunData.bean = res.bean
				}
				// setTimeout(function() {
				// 	// callBack && callBack();
				// }, 2000)
			})
		},
		// 提示开通会员
		promptOpenVip(callBack) {
			uni.showModal({
				title: '系统提示',
				content: '系统检测到用户未开通会员，是否开通会员',
				success: res => {
					if (res.confirm) {
						index.navigate_to(`/pages/vip/vip`);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return index.onShareAppMessage(shareData);
	}
}
